/* block Style */
.blockContainer{
    position:relative;
    display:inline-block;
    text-align: center;
    width: 30vw;
    height: 15vw;
    max-width: 30vw;
    max-height: 15vw;
    overflow: hidden;
    cursor:pointer;
    border-radius: 80%;
}
.blockContainer:hover img {
    transform: scale(150%);
}
.blockTitle{
    position:absolute;
    color:white;
    font-size: 2vw;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%);
}
.bgImg{
    width: 30vw;
    height: 15vw;
    max-width: 30vw;
    max-height: 15vw;
    transition: 1s;
    
}
